@import url(https://fonts.googleapis.com/css2?family=Alata&display=swap);
:root {
    --primary-color: #007afb; 
    --accent-color: #f7a92d;
    --text-color: #121212;
    --overlay-color: rgba(3, 3, 3, 0.7);
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Alata', sans-serif;
    width: 100%;
    height: 100%;
    scroll-behavior: smooth;
}

h2 {
    letter-spacing: 1px;
}

.overlay-color {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(3, 3, 3, 0.7);
    background-color: var(--overlay-color);
}

/* custom styled general button */
.custom-btn {
    padding: 10px;
    width: 200px;
    border: none;
    color: #fefffe;
    background: #007afb;
    background: var(--primary-color);
}

.custom-btn:hover {
    color: #fefffe;
    background: darkblue;
}
.hero-section {
    height: 500px;
    width: 100%;
    background: url(/static/media/hero-greece-img.d259b5f7.jpg)  no-repeat center/cover;
    position: relative;
}

.hero-content {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: left;
    justify-content: center;
    text-align: left;
    margin: auto;
    padding: 5rem 1rem;
    max-width: 100%;
    line-height: 2em;
    color: #fefffe;
}

.hero-content h2 {
    font-size: 4rem;
    font-weight: 700;
    color: var(--accent-color);
}

.hero-content p {
    font-size: 1.5rem;
}

.hero-button {
   padding: 10px;
   width: 200px;
   border: 1px solid #fefffe !important;
   color: #fefffe;
   text-align: center;
   background: transparent;
}

.hero-button:hover {
   border: none !important;
   color: #fefffe;
   background: var(--primary-color) !important;
}

.about-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: auto;
    max-width: 100%;
    padding: 1rem;
    background: var(--primary-color);
    color: #fefffe; 
}

.about-content h2 {
    font-weight: 700;
}

.about-content p {
    display: block;
    padding: 0 4rem;
}

@media screen and (max-width: 480px) {
    .hero-content h2 {
        font-size: 2.5rem;
    }   

    .hero-content p {
        font-size: 1rem;
    }
}

.services-cards {
    padding: 30px;
}

.cards {
    box-shadow: 0 0 10px rgba(169, 169, 169, 0.1);
}

small {
    color: #696969;
}
.cargo-section {
    background: var(--primary-color);
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    text-align: center;
    color: #fefffe;
    padding: 20px;
 }

.success {
    color: green;
}

.error {
    color: red;
}
.footer-section {
    background: #141618;
    color: #f3f6f8;
}

h4 {
    padding: 15px;
}

h6 {
    color: #d5d5d5;
}

.partners-section img {
    width: 100px;
}

.footer-contact p {
    cursor: pointer;
    color: #d5d5d5;
}

.footer-contact p:hover {
    color: #f3f6f8;
}

.newsletter-form {
    background: #d5d5d5;
}

.social-media {
    background: #121212;  
}

.social-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-align: center;
    max-width: 100%;
    padding: 10px;
}

@media screen and (max-width: 480px) {
    .footer-container {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        text-align: center;
    }

    .partners-section {
        margin-bottom: 15px;
    }

    .footer-contact {
        margin-bottom: 15px;
    }
}

@media screen and (max-width: 700px) {
    .footer-container {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        text-align: center;
    }

    .partners-section {
        margin-bottom: 15px;
    }

    .footer-contact {
        margin-bottom: 15px;
    } 
}
.cargo-header {
    height: 500px;
    width: 100%;
    background: url(/static/media/cargo-hero.75e1af96.jpg)  no-repeat center/cover;
    position: relative;
}

.cargo-header-content {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: auto;
    max-width: 100%;
    padding: 5rem 1rem; 
    line-height: 2em;
    color: #fefffe;    
}

.cargo-header-content h2 {
    font-size: 3rem;
    letter-spacing: 1px;
}

span {
    color: var(--primary-color);
}

.cargo-services strong {
    color: var(--primary-color);
}

.cargo-services h4 {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 15px;
}

.cargo-services .cargo-img {
    width: 200px;
}

@media screen and (max-width: 480px) {
    .cargo-header-content h2 {
       font-size: 2.5rem;
    }
    
    h3 {
        font-size: 1.5rem; 
    }
    
    h4 {
        font-size: 1rem;
    }

    .cargo-services .cargo-img {
        max-width: 100%;
        align-items: center;
    }
}
